<script>
	import * as d3 from "d3"

	export let key = ''
	export let options = []
	export let selectedMore = []
	export let selectedLess = []
	// export let total = 10
	// export let title = 'More Like This'

	let show = false;
	let select_all = true;

	let classColor = Array(options.length).fill('default')

	// console.log(key, options)

	function toggleOptions(value) {
		var moreIndex = selectedMore.indexOf(value)
		var lessIndex = selectedLess.indexOf(value)
		if (moreIndex > -1 && lessIndex == -1) {
			selectedMore.splice(moreIndex, 1)
			selectedMore = selectedMore
			selectedLess.push(value)
			selectedLess = selectedLess
		} else if (lessIndex > -1 && moreIndex == -1) {
			selectedLess.splice(lessIndex, 1)
			selectedLess = selectedLess
		} else {
			selectedMore.push(value)
			selectedMore = selectedMore
		}
	}

	$: for (let i = 0; i < options.length; i++) {
		let value = options[i]
		if (selectedMore.indexOf(value) > -1) {
			classColor[i] = 'more'
		} else if (selectedLess.indexOf(value) > -1) {
			classColor[i] = 'less'
		} else {
			classColor[i] = 'default'
		}
	}

</script>

<div>
	<div class={`${key} toggle`}>
		{#each options as value, i}
			<div class={`${classColor[i]} attribute`}
				on:click|stopPropagation={() => toggleOptions(value)}>
				<div class="attributeLeft">{value[0]}</div>
				<div class="attributeRight">{value[1]}</div>
			</div>
		{/each}
	</div>
</div>

<style>
	.toggle {
		display: flex;
    	flex-wrap: wrap;
	}

	.attribute {
		margin-bottom: 5px;
		display: flex;
	    font-size: 12px;
	    width: auto;
	    cursor: pointer;
	}

	.attributeLeft {
		border-radius: 12px 0px 0px 12px;
    	border: steelblue solid 2px;
    	align-content: middle;
    	padding: 0px 5px 1px 10px;
	}

	.attributeRight {
		border-radius: 0px 12px 12px 0px;
    	border-style: solid solid solid hidden;
	    border-color: steelblue;
	    border-width: 2px;
    	align-content: middle;
    	padding: 0px 10px 1px 5px;
	}

	.more {
		background-color: #cde09b;
		margin-right: 0.5em;
	}

	.less {
		background-color: #e0a99b;
		margin-right: 0.5em;
	}

	.default {
		/*background-color: #f4f4f4;*/
		margin-right: 0.5em;
	}
</style>